.me-qestion-input-container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background: #131414;
    border-radius: 16px;
    margin: 16px 0px;
    padding: 16px;
    align-self: stretch;
    flex-grow: 0;
}

.me-qestion-input-text{
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
}

.me-qestion-input-text p {
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
    margin: 2px 0px;
}

.me-qestion-input-text > span{
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #71767A;
    margin: 2px 0px;
}

.me-question-input-action, .me-question-input-action-delete{
    display: flex;
    justify-content: space-between;
    align-self: stretch;
    flex-grow: 0;
    align-items: center;
}

.me-question-input-action button{
    border: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0px 16px;
    width: 106px;
    height: 36px;
    background: #65CC5C;
    border-radius: 100px;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    cursor: pointer;
    user-select: none;
}

.me-question-input-action svg{
    cursor: pointer;
}

.me-qestion-input-delete-container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 16px;
    gap: 16px;
    background: #1D1D1F;
    border-radius: 16px;
    flex: none;
    order: 3;
    align-self: stretch;
    flex-grow: 0;
    margin-bottom: 16px;
}

.me-qestion-input-delete-container p {
    font-weight: 400;
    font-size: 17px;
    line-height: 22px;
    color: #7F8185;
    margin: 0px;
}

.me-question-input-action-delete span {
    font-weight: 800;
    font-size: 15px;
    line-height: 18px;
    color: #7F8185;
    cursor: pointer;
}

.me-question-input-action-delete button {
    border: none;
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    padding: 0px 16px;
    width: 132px;
    height: 36px;
    background: #272729;
    border-radius: 100px;
    font-weight: 800;
    font-size: 15px;
    line-height: 18px;
    color: #7F8185;
}