.me-qestion-out-container-reply{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* padding: 16px 0px; */
    /* height: 126px;    */
    background: #131414;
    border-radius: 16px;
    padding: 16px;
    margin: 16px 0px;
    align-self: stretch;
    flex-grow: 0;
}

.me-qestion-out-text{
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
}

.me-qesrion-out-text-reply p {
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
    align-self: stretch;
    flex-grow: 0;
    margin: 2px 0px;
}

.me-qesrion-out-text-reply > span{
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #D2D3D6;
    margin: 2px 0px;
    align-self: stretch;
    flex-grow: 0;
}

.me-question-out-action{
    display: flex;
    justify-content: space-between;
    align-self: stretch;
    flex-grow: 0;
}

.me-question-out-action button{
    border: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0px 16px;
    width: 106px;
    height: 36px;
    background: #65CC5C;
    border-radius: 100px;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
}

.me-question-out-reply-info{
    display: flex;
    margin-bottom: 12px;
    flex-direction: row;
}

.me-question-out-info-user-photo{
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: #575757;
}

.me-question-out-info-user{
    display: flex;
    flex-direction: column;
    margin: 0px 12px;
    justify-content: center;
}

.me-question-out-info-user p{
    margin: 0px;
    color: white;
}

.me-question-out-info-user span{
    color: #71767A;
}

.me-question-out-reply-like{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0px 12px;
    background: #1D1E1F;
    border-radius: 100px;
}

.me-question-out-reply-like span{
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    color: #71767A;
    margin: 0px 4px;
}

.me-question-out-reply-setting > span{
    margin-right: 12px;
}