#bottom-menu-container{
    display:flex;
    height: 82px;
    background: #1D1E1F;
    justify-content: space-around;
    -webkit-order: 0;
    -ms-flex-order: 0;
    order: 0;
    -webkit-flex: 1 1 auto;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    -webkit-align-self: flex-end;
    -ms-flex-item-align: end;
    align-self: flex-end;
}

.icon-container, .icon-container-select{
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon-select-link{
    pointer-events: none;
    user-select: none;
    cursor: default;
}

.icon-container > svg > path{
    fill: #71767A;
}

.icon-container-select > svg > path {
    fill: #65CC5C;
}

#position-menu-container{
    position: fixed;
    bottom: 0;
    width: 100%;
}